import React, {useState, useEffect} from 'react'
import axios from 'axios';

function Games(){
    const [games, setGames] = useState([]);
    useEffect(()=>{
        axios.get('/getgames')
        .then(response=>{
            setGames(response.data);
        })
        .catch(error=>console.log(error))
    }, []);

    return(
        <body>
            <h3> </h3>
            <table>
                <th align="center"> </th>
                <th align="center"></th>
                <th align="center"></th>
                <th align="center"> </th>
                <th align="center"></th>
                {games.map((game) => (
                    <tr align="center">
                        <td>
                            {game["game_number"]}
                        </td>
                        <td>
                            {game["win_user"]}({game["win_race"]})
                        </td>
                        <td>
                            {game["lose_user"]}({game["lose_race"]})
                        </td>
                        <td>
                            {game["point"]}
                        </td>
                        <td>
                            {game["writer"]}
                        </td>
                    </tr>
                ))}
            </table>
        </body>
    );
}

export default Games
