import React, {useState, useEffect} from 'react'
import axios from 'axios';

function AddGame(){
    const valueCheck = ()=>{
        const win_user = document.getElementById("win_user");
        const lose_user = document.getElementById("lose_user");
        const win_race = document.getElementById("win_race");
        const lose_race = document.getElementById("lose_race");

        if(win_user.value === ""){
            alert('  .');
            win_user.focus();
            return false;
        }
        if(lose_user.value === ""){
            alert('  .');
            lose_user.focus();
            return false;
        }
        if(win_race.value === ""){
            alert('   .');
            win_race.focus();
            return false;
        }
        if(lose_race.value === ""){
            alert('   .');
            lose_race.focus();
            return false;
        }

        if(win_user.value === lose_user.value){
            alert('    .');
            lose_user.focus();
            return false;
        }

        document.AddGameForm.submit();
    }
    
    const [gamers, setGamers] = useState([]);
    useEffect(()=>{
        axios.get('/getgamers')
        .then(response=>{
            setGamers(response.data);
        })
        .catch(error=>console.log(error))
    }, []);
    
    return(
        <body>
            <h3> </h3>
            <div id='addgame'>
                <form id="AddGameForm" name="AddGameForm" action="/addgame" method="post">
                    <div>
                        <label for="win_user"> </label>
                        <select name="win_user" id="win_user">
                            <option value=""> </option>
                            {gamers.map((name) => (
                                <option value={name} key={name}>
                                    {name}
                                </option>
                            ))}
                        </select>
                    </div>
                    <div>
                        <label for="win_race">  </label>
                        <select name="win_race" id="win_race">
                            <option value=""> </option>
                            <option value="Terran">Terran</option>
                            <option value="Protoss">Protoss</option>
                            <option value="Zerg">Zerg</option>
                        </select>
                    </div>
                    <div>
                        <label for="lose_user"> </label>
                        <select name="lose_user" id="lose_user">
                            <option value=""> </option>
                            {gamers.map((name) => (
                                <option value={name} key={name}>
                                    {name}
                                </option>
                            ))}
                        </select>
                    </div>
                    <div>
                        <label for="lose_race">  </label>
                        <select name="lose_race" id="lose_race">
                            <option value=""> </option>
                            <option value="Terran">Terran</option>
                            <option value="Protoss">Protoss</option>
                            <option value="Zerg">Zerg</option>
                        </select>
                    </div>
                    <div>
                        <button type="button" onClick={valueCheck}> </button>
                    </div>
                </form>
            </div>
        </body>
    )
}

export default AddGame
