import React from 'react'

function AddGamer(){
    return(
        <body>
            <h3> </h3>
            <div id='addgamer'>
                <form id="AddGamerForm" action="/addgamer" method="post">
                    <div>
                        <label for="name"></label>
                        <input name="name" id="name" required minlength='1' maxlength='10' type="text" placeholder="Name" title="1~10 ." />
                    </div>
                    <div>
                        <button type="submit"> </button>
                    </div>
                </form>
            </div>
        </body>
        
    )
}

export default AddGamer
